@import url(https://fonts.googleapis.com/css?family=Baloo&display=swap);
@-webkit-keyframes boom {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 1;
	}

	80% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 0;
	}
}

@keyframes boom {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 1;
	}

	80% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 0;
	}
}

@-webkit-keyframes opacity {
	0% {
		opacity: 1;
	}

	20% {
		opacity: 0.5;
	}

	100% {
		opacity: 1;
	}
}

@keyframes opacity {
	0% {
		opacity: 1;
	}

	20% {
		opacity: 0.5;
	}

	100% {
		opacity: 1;
	}
}

@-webkit-keyframes scale {
	0% {
		-webkit-transform: scale(0.3);
		        transform: scale(0.3);
		opacity: 0.5;
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

@keyframes scale {
	0% {
		-webkit-transform: scale(0.3);
		        transform: scale(0.3);
		opacity: 0.5;
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}
}

@-webkit-keyframes scaleOut {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}

	50% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0.5;
	}

	100% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0.5;
	}
}

@keyframes scaleOut {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;
	}

	50% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0.5;
	}

	100% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 0.5;
	}
}

@-webkit-keyframes slideIn {
	from {
		-webkit-transform: translateX(300px);
		        transform: translateX(300px);
	}

	to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@keyframes slideIn {
	from {
		-webkit-transform: translateX(300px);
		        transform: translateX(300px);
	}

	to {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@-webkit-keyframes slideOut {
	from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}

	to {
		-webkit-transform: translateX(2000px);
		        transform: translateX(2000px);
	}
}

@keyframes slideOut {
	from {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}

	to {
		-webkit-transform: translateX(2000px);
		        transform: translateX(2000px);
	}
}

@-webkit-keyframes floatBottom {
	from {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
		opacity: 0;
	}

	t0 {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
		opacity: 1;
	}
}

@keyframes floatBottom {
	from {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
		opacity: 0;
	}

	t0 {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
		opacity: 1;
	}
}

@-webkit-keyframes rotate {
	0% {
		-webkit-transform: rotate(50deg);
		        transform: rotate(50deg);
		opacity: 0;
	}

	60% {
		-webkit-transform: rotate(50deg);
		        transform: rotate(50deg);
		opacity: 0;
	}

	100% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
		opacity: 1;
	}
}

@keyframes rotate {
	0% {
		-webkit-transform: rotate(50deg);
		        transform: rotate(50deg);
		opacity: 0;
	}

	60% {
		-webkit-transform: rotate(50deg);
		        transform: rotate(50deg);
		opacity: 0;
	}

	100% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
		opacity: 1;
	}
}

@keyframes boom {
	0% {
		-webkit-transform: scale(0);
		        transform: scale(0);
		opacity: 1;
	}

	80% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 0;
	}
}

@-webkit-keyframes arrow {
	0% {
		opacity: 0;
		-webkit-transform: scale(0.8) translateX(-200px);
		        transform: scale(0.8) translateX(-200px);
	}
	50% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		opacity: 1;
	}
}

@keyframes arrow {
	0% {
		opacity: 0;
		-webkit-transform: scale(0.8) translateX(-200px);
		        transform: scale(0.8) translateX(-200px);
	}
	50% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		opacity: 1;
	}
}

@-webkit-keyframes hide {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes hide {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@-webkit-keyframes load {
	0% {
		-webkit-transform: scale(1)  rotate(0);
		        transform: scale(1)  rotate(0);
	}

	50% {
		-webkit-transform: scale(0.3) rotate(360deg);
		        transform: scale(0.3) rotate(360deg)
	}

	100% {
		-webkit-transform: scale(1) rotate(710deg);
		        transform: scale(1) rotate(710deg);
	}
}

@keyframes load {
	0% {
		-webkit-transform: scale(1)  rotate(0);
		        transform: scale(1)  rotate(0);
	}

	50% {
		-webkit-transform: scale(0.3) rotate(360deg);
		        transform: scale(0.3) rotate(360deg)
	}

	100% {
		-webkit-transform: scale(1) rotate(710deg);
		        transform: scale(1) rotate(710deg);
	}
}

.lottie {
	position: absolute;
	top: 0;
	left: 0;
}

* {
  --color-main: lightgreen;
  --color-decorative: gold;
  --color-primary: lightgray;
  --color-secondary: gray;
  --color-bg: #202020;
  --color-light: white;
  --color-dark: black;
  --color-fb: #3b5998;
  --color-gplus: #d34836;
  --color-yt: #c4302b;
  box-sizing: border-box;
}
*::after,
*::before {
  box-sizing: border-box;
}
body {
  font-family: 'Baloo', sans-serif;
  background-color: var(--color-bg);
  color: var(--color-primary);
  width: 100vw;
  height: 100vh;
  margin: 0;
}
button {
  font-family: 'Baloo', sans-serif;
}
